import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Paper from "@material-ui/core/Paper";
import Typography from "@material-ui/core/Typography";
import Card from "@material-ui/core/Card";
import CardActionArea from "@material-ui/core/CardActionArea";
import CardMedia from "@material-ui/core/CardMedia";

const useStyles = makeStyles(theme => ({
  root: {
    padding: theme.spacing(3, 2)
  },
  card: {
    maxWidth: 345,
    padding: 20
  },
  typo: {
    justifyContent: "center"
  },
  div: {
    display: "grid",
    gridGap: 10,
    gridTemplateColumns: "repeat(auto-fit, minmax(240px, 1fr))",
    marginTop: 20
  },
  media: {
    height: 140
  }
}));

export default function GalleryBanner() {
  const classes = useStyles();

  return (
    <Paper className={(classes.root, classes.div)}>
      <Card className={classes.card}>
        <CardActionArea>
          <CardMedia
            className={classes.media}
            image="images2/IMG2.jpg"
            title="Contemplative Reptile"
          />
        </CardActionArea>
      </Card>
      <Typography variant="h6" component="h6" className={classes.typo}>
        The landscape of the painting is associated with the vacation, which the
        viewer wants so much to spend on the shores of the azure sea, far from
        the hustle and bustle of daily worries, plunging into peace and quiet.
      </Typography>
    </Paper>
  );
}
